/**
 * Session Management for Mentimeter Pro
 */

// Characters that are unambiguous (no O/0, I/1/L confusion)
const SAFE_CHARS = 'ABCDEFGHJKMNPQRSTUVWXYZ23456789'

/**
 * Generate a random session code
 * @param {number} length - Length of the code (default: 6)
 * @returns {string} The session code
 */
export function generateSessionCode(length = 6) {
  let code = ''
  const randomValues = new Uint8Array(length)
  crypto.getRandomValues(randomValues)
  
  for (let i = 0; i < length; i++) {
    code += SAFE_CHARS[randomValues[i] % SAFE_CHARS.length]
  }
  
  return code
}

/**
 * Create a new session object
 * @returns {Session} A new session instance
 */
export function createSession() {
  return new Session()
}

/**
 * Session class for managing a presentation session
 */
export class Session {
  constructor() {
    this.code = generateSessionCode()
    this.presenterId = crypto.randomUUID()
    this.createdAt = Date.now()
    this.polls = []
    this.participants = []
    this.activePollId = null
  }

  /**
   * Add a participant to the session
   * @param {string} id - Unique participant ID
   * @param {string} name - Display name
   */
  addParticipant(id, name) {
    if (this.participants.some(p => p.id === id)) {
      return // Already exists
    }
    this.participants.push({
      id,
      name,
      joinedAt: Date.now()
    })
  }

  /**
   * Remove a participant from the session
   * @param {string} id - Participant ID to remove
   */
  removeParticipant(id) {
    this.participants = this.participants.filter(p => p.id !== id)
  }

  /**
   * Get the number of participants
   * @returns {number} Participant count
   */
  getParticipantCount() {
    return this.participants.length
  }

  /**
   * Add a poll to the session
   * @param {Object} poll - Poll configuration
   * @returns {Object} The created poll with ID
   */
  addPoll(poll) {
    const pollWithId = {
      ...poll,
      id: crypto.randomUUID()
    }
    this.polls.push(pollWithId)
    return pollWithId
  }

  /**
   * Set the currently active poll
   * @param {string} pollId - ID of the poll to activate
   */
  setActivePoll(pollId) {
    this.activePollId = pollId
  }

  /**
   * Get the currently active poll
   * @returns {Object|null} The active poll or null
   */
  getActivePoll() {
    return this.polls.find(p => p.id === this.activePollId) || null
  }

  /**
   * Serialize session to JSON
   * @returns {Object} JSON representation
   */
  toJSON() {
    return {
      code: this.code,
      presenterId: this.presenterId,
      createdAt: this.createdAt,
      polls: this.polls,
      participants: this.participants,
      activePollId: this.activePollId
    }
  }

  /**
   * Restore session from JSON
   * @param {Object} json - JSON representation
   * @returns {Session} Restored session
   */
  static fromJSON(json) {
    const session = new Session()
    session.code = json.code
    session.presenterId = json.presenterId
    session.createdAt = json.createdAt
    session.polls = json.polls || []
    session.participants = json.participants || []
    session.activePollId = json.activePollId || null
    return session
  }
}
