/**
 * Screen Components
 */

import { h } from './render.js'
import QRCode from 'qrcode'

export function HomeScreen({ onStartSession, onJoinSession }) {
  const joinInput = h('input', {
    type: 'text',
    className: 'input',
    placeholder: 'ABC123',
    maxlength: '6'
  })
  
  const nameInput = h('input', {
    type: 'text',
    className: 'input',
    placeholder: 'Dein Name',
    style: { marginTop: '0.5rem' }
  })

  const handleJoin = () => {
    const code = joinInput.value.trim().toUpperCase()
    const name = nameInput.value.trim() || 'Anonym'
    if (code.length >= 4) {
      onJoinSession(code, name)
    }
  }

  return h('div', { className: 'home-screen' },
    h('div', { className: 'logo' }, '📊'),
    h('h1', {}, 'Mentimeter Pro'),
    h('p', { className: 'tagline' }, 'Live Polls & Feedback für Workshops'),
    
    h('div', { className: 'home-actions' },
      h('button', {
        className: 'btn btn-primary btn-large btn-full',
        onClick: onStartSession
      }, '🎤 Präsentation starten'),
      
      h('div', { className: 'divider' }, 'oder'),
      
      h('div', {},
        joinInput,
        nameInput,
        h('button', {
          className: 'btn btn-secondary btn-full',
          style: { marginTop: '0.5rem' },
          onClick: handleJoin
        }, '🚀 Session beitreten')
      )
    )
  )
}

export function WaitingRoom({ session, participantCount, onCreatePoll }) {
  const qrContainer = h('div', { className: 'qr-container', id: 'qr-code' })
  
  // Generate QR code
  const joinUrl = `${window.location.origin}${window.location.pathname}?join=${session.code}`
  QRCode.toCanvas(qrContainer, joinUrl, {
    width: 200,
    margin: 0,
    color: { dark: '#1e293b', light: '#ffffff' }
  }).catch(console.error)

  return h('div', { className: 'container text-center', style: { paddingTop: '2rem' } },
    h('h2', { className: 'mb-2' }, 'Session bereit!'),
    
    h('div', { className: 'session-code mb-3' }, session.code),
    
    qrContainer,
    
    h('p', { className: 'text-muted mt-2 mb-3' },
      'Scanne den QR-Code oder gib den Code ein'
    ),
    
    h('div', { className: 'participant-count mb-3', style: { display: 'inline-flex' } },
      h('span', { className: 'participant-dot' }),
      h('span', {}, `${participantCount} Teilnehmer`)
    ),
    
    h('div', { className: 'card mt-3' },
      h('h3', { className: 'mb-2' }, 'Neue Umfrage erstellen'),
      h('div', { className: 'flex flex-col gap-1' },
        h('button', {
          className: 'btn btn-secondary btn-full',
          onClick: () => onCreatePoll('multiple-choice')
        }, '📊 Multiple Choice'),
        h('button', {
          className: 'btn btn-secondary btn-full',
          onClick: () => onCreatePoll('word-cloud')
        }, '☁️ Word Cloud'),
        h('button', {
          className: 'btn btn-secondary btn-full',
          onClick: () => onCreatePoll('open-ended')
        }, '💬 Offene Frage')
      )
    )
  )
}

export function PollCreator({ type, onSave, onCancel }) {
  const questionInput = h('input', {
    type: 'text',
    className: 'input',
    placeholder: 'Deine Frage...'
  })

  let optionsContainer = null
  let options = ['', '']

  const createOptionRow = (index) => {
    const input = h('input', {
      type: 'text',
      className: 'input',
      placeholder: `Option ${index + 1}`,
      value: options[index] || ''
    })
    input.addEventListener('input', () => {
      options[index] = input.value
    })

    return h('div', { className: 'option-row' },
      input,
      index > 1 ? h('button', {
        className: 'btn btn-icon btn-secondary',
        onClick: () => {
          options.splice(index, 1)
          renderOptions()
        }
      }, '✕') : null
    )
  }

  const renderOptions = () => {
    if (!optionsContainer) return
    optionsContainer.innerHTML = ''
    options.forEach((_, i) => {
      optionsContainer.appendChild(createOptionRow(i))
    })
  }

  if (type === 'multiple-choice') {
    optionsContainer = h('div', { className: 'option-list' })
    renderOptions()
  }

  const allowMultipleCheckbox = type === 'multiple-choice' 
    ? h('label', { className: 'flex items-center gap-1', style: { cursor: 'pointer' } },
        h('input', { type: 'checkbox', id: 'allow-multiple' }),
        'Mehrere Antworten erlauben'
      )
    : null

  const handleSave = () => {
    const question = questionInput.value.trim()
    if (!question) return

    const config = { question }

    if (type === 'multiple-choice') {
      config.options = options.filter(o => o.trim())
      if (config.options.length < 2) {
        alert('Mindestens 2 Optionen benötigt')
        return
      }
      const checkbox = document.getElementById('allow-multiple')
      config.allowMultiple = checkbox?.checked || false
    }

    onSave(type, config)
  }

  return h('div', { className: 'container' },
    h('div', { className: 'card' },
      h('h2', { className: 'mb-2' }, 
        type === 'multiple-choice' ? '📊 Multiple Choice' :
        type === 'word-cloud' ? '☁️ Word Cloud' : '💬 Offene Frage'
      ),
      
      h('div', { className: 'poll-form' },
        h('div', {},
          h('label', { className: 'text-muted mb-1', style: { display: 'block' } }, 'Frage'),
          questionInput
        ),
        
        optionsContainer ? h('div', {},
          h('label', { className: 'text-muted mb-1', style: { display: 'block' } }, 'Antwortmöglichkeiten'),
          optionsContainer,
          h('button', {
            className: 'btn btn-secondary mt-2',
            onClick: () => {
              options.push('')
              renderOptions()
            }
          }, '+ Option hinzufügen')
        ) : null,
        
        allowMultipleCheckbox,
        
        h('div', { className: 'flex gap-1 mt-2' },
          h('button', { className: 'btn btn-secondary', onClick: onCancel }, 'Abbrechen'),
          h('button', { className: 'btn btn-primary', onClick: handleSave }, 'Umfrage starten')
        )
      )
    )
  )
}

export function PresenterPollView({ poll, type, participantCount }) {
  let content
  
  if (type === 'multiple-choice') {
    const results = poll.getResults()
    const percentages = poll.getPercentages()
    const total = poll.getTotalVotes()
    
    content = h('div', { className: 'flex flex-col gap-1' },
      ...poll.options.map(option => {
        const percent = percentages[option] || 0
        const count = results[option] || 0
        
        return h('div', { className: 'poll-option' },
          h('div', { 
            className: 'poll-option-bar',
            style: { width: `${percent}%` }
          }),
          h('div', { className: 'poll-option-content' },
            h('span', { className: 'poll-option-text' }, option),
            h('span', { className: 'poll-option-percent' }, 
              `${count} (${percent}%)`
            )
          )
        )
      })
    )
  } else if (type === 'word-cloud') {
    const words = poll.getWordsWithSizes(20, 56)
    
    content = h('div', { className: 'word-cloud' },
      words.length === 0 
        ? h('p', { className: 'text-muted' }, 'Warte auf Antworten...')
        : words.map(w => 
            h('span', { 
              className: 'word-cloud-item',
              style: { fontSize: `${w.size}px` }
            }, w.text)
          )
    )
  } else if (type === 'open-ended') {
    const responses = poll.getResponses()
    
    content = h('div', { className: 'response-list' },
      responses.length === 0
        ? h('p', { className: 'text-muted text-center' }, 'Warte auf Antworten...')
        : responses.map(r => 
            h('div', { className: 'response-item' }, r.text)
          )
    )
  }
  
  return h('div', { className: 'container-wide', style: { padding: '2rem' } },
    h('div', { className: 'flex justify-between items-center mb-3' },
      h('div', { className: 'participant-count' },
        h('span', { className: 'participant-dot' }),
        h('span', {}, `${participantCount} Teilnehmer`)
      )
    ),
    
    h('div', { className: 'card' },
      h('h2', { className: 'mb-3' }, poll.question),
      content
    )
  )
}

export function ParticipantWaiting({ sessionCode }) {
  return h('div', { className: 'container text-center', style: { paddingTop: '4rem' } },
    h('div', { className: 'session-code session-code-small mb-3' }, sessionCode),
    h('h2', { className: 'mb-2' }, 'Warte auf den Presenter...'),
    h('div', { className: 'waiting-animation', style: { justifyContent: 'center' } },
      h('div', { className: 'waiting-dot' }),
      h('div', { className: 'waiting-dot' }),
      h('div', { className: 'waiting-dot' })
    )
  )
}

export function ParticipantPollView({ poll, type, onSubmit }) {
  if (type === 'multiple-choice') {
    const selected = new Set()
    const allowMultiple = poll.allowMultiple
    
    const optionElements = poll.options.map(option => {
      const el = h('div', { 
        className: 'poll-option',
        onClick: () => {
          if (allowMultiple) {
            if (selected.has(option)) {
              selected.delete(option)
              el.classList.remove('selected')
            } else {
              selected.add(option)
              el.classList.add('selected')
            }
          } else {
            // Deselect all others
            optionElements.forEach(oe => oe.classList.remove('selected'))
            selected.clear()
            selected.add(option)
            el.classList.add('selected')
          }
        }
      },
        h('div', { className: 'poll-option-content' },
          h('span', { className: 'poll-option-text' }, option)
        )
      )
      return el
    })

    return h('div', { className: 'container' },
      h('div', { className: 'card' },
        h('h2', { className: 'mb-3' }, poll.question),
        h('div', { className: 'flex flex-col gap-1' }, ...optionElements),
        h('button', {
          className: 'btn btn-primary btn-full mt-3',
          onClick: () => {
            if (selected.size > 0) {
              onSubmit(Array.from(selected))
            }
          }
        }, 'Abstimmen')
      )
    )
  }
  
  if (type === 'word-cloud') {
    const input = h('input', {
      type: 'text',
      className: 'input',
      placeholder: 'Wörter mit Komma trennen (z.B. kreativ, spannend, lehrreich)'
    })

    return h('div', { className: 'container' },
      h('div', { className: 'card' },
        h('h2', { className: 'mb-3' }, poll.question),
        input,
        h('button', {
          className: 'btn btn-primary btn-full mt-2',
          onClick: () => {
            const words = input.value.split(',').map(w => w.trim()).filter(Boolean)
            if (words.length > 0) {
              onSubmit(words)
              input.value = ''
              input.disabled = true
            }
          }
        }, 'Absenden')
      )
    )
  }
  
  if (type === 'open-ended') {
    const textarea = h('textarea', {
      className: 'textarea',
      placeholder: 'Deine Antwort...'
    })

    return h('div', { className: 'container' },
      h('div', { className: 'card' },
        h('h2', { className: 'mb-3' }, poll.question),
        textarea,
        h('button', {
          className: 'btn btn-primary btn-full mt-2',
          onClick: () => {
            const text = textarea.value.trim()
            if (text) {
              onSubmit(text)
              textarea.value = ''
            }
          }
        }, 'Absenden')
      )
    )
  }
}

export function ConnectionStatus({ status }) {
  const statusClass = status === 'connected' ? 'connected' 
    : status === 'connecting' ? 'connecting' : 'disconnected'
  
  const statusText = status === 'connected' ? 'Verbunden'
    : status === 'connecting' ? 'Verbinde...' : 'Getrennt'

  return h('div', { className: `status-badge ${statusClass}` },
    h('span', { className: 'participant-dot' }),
    statusText
  )
}

export function ErrorScreen({ message, onRetry }) {
  return h('div', { className: 'container text-center', style: { paddingTop: '4rem' } },
    h('div', { style: { fontSize: '4rem', marginBottom: '1rem' } }, '😕'),
    h('h2', { className: 'mb-2' }, 'Etwas ist schiefgelaufen'),
    h('p', { className: 'text-muted mb-3' }, message),
    h('button', { className: 'btn btn-primary', onClick: onRetry }, 'Erneut versuchen')
  )
}
