/**
 * P2P Connection Manager for Mentimeter Pro
 * Uses PeerJS for WebRTC-based peer-to-peer connections
 */

export const MESSAGE_TYPES = {
  JOIN: 'join',
  LEAVE: 'leave',
  VOTE: 'vote',
  SUBMIT: 'submit',
  SYNC_STATE: 'sync-state',
  POLL_UPDATE: 'poll-update',
  PARTICIPANT_UPDATE: 'participant-update'
}

/**
 * Simple event emitter mixin
 */
class EventEmitter {
  constructor() {
    this._eventHandlers = new Map()
  }

  on(event, handler) {
    if (!this._eventHandlers.has(event)) {
      this._eventHandlers.set(event, [])
    }
    this._eventHandlers.get(event).push(handler)
    return this
  }

  off(event, handler) {
    if (!this._eventHandlers.has(event)) return
    const handlers = this._eventHandlers.get(event)
    const index = handlers.indexOf(handler)
    if (index > -1) handlers.splice(index, 1)
    return this
  }

  emit(event, data) {
    if (!this._eventHandlers.has(event)) return
    for (const handler of this._eventHandlers.get(event)) {
      try {
        handler(data)
      } catch (e) {
        console.error('Event handler error:', e)
      }
    }
  }
}

/**
 * P2P Manager for handling peer connections
 */
export class P2PManager extends EventEmitter {
  constructor(options = {}) {
    super()
    this.peer = null
    this.peerId = null
    this.isHost = false
    this._connections = new Map()
    this._initialized = false
    this._PeerClass = options.PeerClass || (typeof Peer !== 'undefined' ? Peer : null)
    this._sessionCode = null
  }

  /**
   * Convert session code to consistent peer ID
   * @param {string} sessionCode - Session code
   * @returns {string} Peer ID
   */
  sessionCodeToPeerId(sessionCode) {
    // Create a deterministic peer ID from session code
    return `mentimeter-pro-${sessionCode.toLowerCase()}`
  }

  /**
   * Initialize the P2P manager
   * @param {string} [peerId] - Optional specific peer ID
   * @returns {Promise<string>} The peer ID
   */
  async init(peerId = null) {
    if (this._initialized) {
      return this.peerId
    }

    return new Promise((resolve, reject) => {
      try {
        this.peer = new this._PeerClass(peerId, {
          debug: 0
        })

        this.peer.on('open', (id) => {
          this.peerId = id
          this._initialized = true
          this.emit('ready', id)
          resolve(id)
        })

        this.peer.on('error', (err) => {
          this.emit('error', err)
          reject(err)
        })

        this.peer.on('connection', (conn) => {
          this._handleConnection(conn)
        })

      } catch (err) {
        reject(err)
      }
    })
  }

  /**
   * Start hosting a session
   * @param {string} sessionCode - Session code to host
   * @returns {string} The session code
   */
  host(sessionCode) {
    this._sessionCode = sessionCode
    this.isHost = true
    return sessionCode
  }

  /**
   * Join a session as a participant
   * @param {string} hostPeerId - Peer ID of the host
   * @returns {Promise<void>}
   */
  join(hostPeerId) {
    this.isHost = false
    
    const conn = this.peer.connect(hostPeerId)
    this._connections.set(hostPeerId, conn)
    
    conn.on('open', () => {
      this.emit('joined', hostPeerId)
    })

    conn.on('data', (data) => {
      this.emit('message', {
        ...data,
        from: hostPeerId
      })
    })

    conn.on('close', () => {
      this._connections.delete(hostPeerId)
      this.emit('disconnected')
    })

    return conn
  }

  /**
   * Handle incoming connection
   * @param {DataConnection} conn - PeerJS data connection
   */
  _handleConnection(conn) {
    const peerId = conn.peer
    
    conn.on('open', () => {
      this._connections.set(peerId, conn)
      this.emit('peer-connect', peerId)
    })

    conn.on('data', (data) => {
      this.emit('message', {
        ...data,
        from: peerId
      })
    })

    conn.on('close', () => {
      this._connections.delete(peerId)
      this.emit('peer-disconnect', peerId)
    })

    conn.on('error', (err) => {
      console.error('Connection error:', err)
      this.emit('peer-error', { peerId, error: err })
    })
  }

  /**
   * Get list of connected peer IDs
   * @returns {string[]} Array of peer IDs
   */
  getConnectedPeers() {
    return Array.from(this._connections.keys())
  }

  /**
   * Broadcast a message to all connected peers
   * @param {Object} message - Message to broadcast
   */
  broadcast(message) {
    for (const conn of this._connections.values()) {
      try {
        conn.send(message)
      } catch (err) {
        console.error('Broadcast error:', err)
      }
    }
  }

  /**
   * Send a message to the host (for guests)
   * @param {Object} message - Message to send
   */
  sendToHost(message) {
    // For guests, there should be exactly one connection - to the host
    const conn = this._connections.values().next().value
    if (conn) {
      conn.send(message)
    }
  }

  /**
   * Send a message to a specific peer
   * @param {string} peerId - Target peer ID
   * @param {Object} message - Message to send
   */
  sendTo(peerId, message) {
    const conn = this._connections.get(peerId)
    if (conn) {
      conn.send(message)
    }
  }

  /**
   * Destroy the manager and close all connections
   */
  destroy() {
    for (const conn of this._connections.values()) {
      try {
        conn.close()
      } catch (e) {
        // Ignore close errors
      }
    }
    this._connections.clear()
    
    if (this.peer) {
      this.peer.destroy()
    }
    
    this._initialized = false
    this.peerId = null
  }
}
