/**
 * Mentimeter Pro - Main Entry Point
 */

import { App } from './lib/app.js'
import { render, $ } from './components/render.js'
import {
  HomeScreen,
  WaitingRoom,
  PollCreator,
  PresenterPollView,
  ParticipantWaiting,
  ParticipantPollView,
  ConnectionStatus,
  ErrorScreen
} from './components/screens.js'

// Global app instance
const app = new App()

// Current UI state
let currentScreen = 'home'
let participantCount = 0
let activePollId = null
let creatingPollType = null

// Main render function
function renderApp() {
  const container = $('#app')
  
  switch (currentScreen) {
    case 'home':
      render(container, HomeScreen({
        onStartSession: handleStartSession,
        onJoinSession: handleJoinSession
      }))
      break
      
    case 'waiting-room':
      render(container, WaitingRoom({
        session: app.session,
        participantCount,
        onCreatePoll: handleCreatePoll
      }))
      break
      
    case 'creating-poll':
      render(container, PollCreator({
        type: creatingPollType,
        onSave: handleSavePoll,
        onCancel: () => {
          currentScreen = 'waiting-room'
          renderApp()
        }
      }))
      break
      
    case 'presenter-poll':
      const presenterPoll = app.getPoll(activePollId)
      const pollInfo = app.session.polls.find(p => p.id === activePollId)
      if (presenterPoll && pollInfo) {
        render(container, PresenterPollView({
          poll: presenterPoll,
          type: pollInfo.type,
          participantCount
        }))
      }
      break
      
    case 'participant-waiting':
      render(container, ParticipantWaiting({
        sessionCode: app.session?.code || '...'
      }))
      break
      
    case 'participant-poll':
      const participantPoll = app.getPoll(activePollId)
      const pPollInfo = app.session?.polls.find(p => p.id === activePollId)
      if (participantPoll && pPollInfo) {
        render(container, ParticipantPollView({
          poll: participantPoll,
          type: pPollInfo.type,
          onSubmit: (data) => handleParticipantSubmit(activePollId, pPollInfo.type, data)
        }))
      }
      break
      
    case 'error':
      render(container, ErrorScreen({
        message: 'Verbindung fehlgeschlagen',
        onRetry: () => {
          currentScreen = 'home'
          renderApp()
        }
      }))
      break
  }
}

// Event Handlers
async function handleStartSession() {
  try {
    await app.startSession()
    currentScreen = 'waiting-room'
    renderApp()
  } catch (err) {
    console.error('Failed to start session:', err)
    currentScreen = 'error'
    renderApp()
  }
}

async function handleJoinSession(code, name) {
  try {
    await app.joinSession(code, name)
    currentScreen = 'participant-waiting'
    renderApp()
  } catch (err) {
    console.error('Failed to join session:', err)
    currentScreen = 'error'
    renderApp()
  }
}

function handleCreatePoll(type) {
  creatingPollType = type
  currentScreen = 'creating-poll'
  renderApp()
}

function handleSavePoll(type, config) {
  const poll = app.createPoll(type, config)
  activePollId = poll.id
  app.activatePoll(poll.id)
  currentScreen = 'presenter-poll'
  renderApp()
}

function handleParticipantSubmit(pollId, type, data) {
  if (type === 'multiple-choice') {
    app.submitVote(pollId, data)
  } else if (type === 'word-cloud') {
    app.submitWords(pollId, data)
  } else if (type === 'open-ended') {
    app.submitText(pollId, data)
  }
  
  // Show waiting state after submission
  currentScreen = 'participant-waiting'
  renderApp()
}

// App Event Listeners
app.on('participants-update', (participants) => {
  participantCount = participants.length
  renderApp()
})

app.on('poll-update', ({ pollId }) => {
  if (pollId === activePollId && app.mode === 'presenter') {
    renderApp()
  }
})

app.on('session-sync', (session) => {
  if (app.mode === 'participant') {
    if (session.activePollId) {
      activePollId = session.activePollId
      currentScreen = 'participant-poll'
    } else {
      currentScreen = 'participant-waiting'
    }
    renderApp()
  }
})

app.on('active-poll-change', (pollId) => {
  activePollId = pollId
  if (app.mode === 'participant' && pollId) {
    currentScreen = 'participant-poll'
    renderApp()
  }
})

app.on('status-change', (status) => {
  console.log('Connection status:', status)
  if (status === 'disconnected' && app.mode === 'participant') {
    // Could show reconnection UI here
  }
})

app.on('error', (err) => {
  console.error('App error:', err)
  currentScreen = 'error'
  renderApp()
})

// Check for join code in URL
function checkUrlParams() {
  const params = new URLSearchParams(window.location.search)
  const joinCode = params.get('join')
  
  if (joinCode) {
    // Pre-fill the join code
    setTimeout(() => {
      const input = document.querySelector('.join-form input, input[placeholder="ABC123"]')
      if (input) {
        input.value = joinCode.toUpperCase()
      }
    }, 100)
  }
}

// Initialize
document.addEventListener('DOMContentLoaded', () => {
  renderApp()
  checkUrlParams()
})

// Handle page unload
window.addEventListener('beforeunload', () => {
  app.destroy()
})

// Export for debugging
window.mentimeterApp = app
