/**
 * App State Manager
 */

import { Session } from './session.js'
import { createPoll, MultipleChoicePoll, WordCloudPoll, OpenEndedPoll } from './polls.js'
import { P2PManager, MESSAGE_TYPES } from './p2p.js'

export class App {
  constructor() {
    this.session = null
    this.p2p = null
    this.mode = null // 'presenter' | 'participant'
    this.participantName = ''
    this.connectionStatus = 'disconnected'
    this._listeners = new Map()
    this._pollInstances = new Map()
  }

  /**
   * Subscribe to app state changes
   */
  on(event, handler) {
    if (!this._listeners.has(event)) {
      this._listeners.set(event, [])
    }
    this._listeners.get(event).push(handler)
  }

  _emit(event, data) {
    const handlers = this._listeners.get(event) || []
    handlers.forEach(h => h(data))
  }

  /**
   * Start a new presentation session
   */
  async startSession() {
    this.session = new Session()
    this.mode = 'presenter'
    
    this.p2p = new P2PManager()
    
    const peerId = this.p2p.sessionCodeToPeerId(this.session.code)
    
    try {
      await this.p2p.init(peerId)
      this.p2p.host(this.session.code)
      this.connectionStatus = 'connected'
      
      this._setupPresenterHandlers()
      this._emit('session-started', this.session)
      this._emit('status-change', this.connectionStatus)
      
    } catch (err) {
      console.error('Failed to start session:', err)
      this.connectionStatus = 'error'
      this._emit('error', err)
    }
    
    return this.session
  }

  /**
   * Join an existing session
   */
  async joinSession(code, name) {
    this.participantName = name
    this.mode = 'participant'
    this.connectionStatus = 'connecting'
    this._emit('status-change', this.connectionStatus)
    
    this.p2p = new P2PManager()
    
    try {
      await this.p2p.init()
      
      const hostPeerId = this.p2p.sessionCodeToPeerId(code.toUpperCase())
      this.p2p.join(hostPeerId)
      
      this._setupParticipantHandlers()
      
      // Send join message
      this.p2p.on('joined', () => {
        this.connectionStatus = 'connected'
        this._emit('status-change', this.connectionStatus)
        
        this.p2p.sendToHost({
          type: MESSAGE_TYPES.JOIN,
          name: this.participantName
        })
      })
      
    } catch (err) {
      console.error('Failed to join session:', err)
      this.connectionStatus = 'error'
      this._emit('error', err)
    }
  }

  _setupPresenterHandlers() {
    this.p2p.on('peer-connect', (peerId) => {
      console.log('Peer connected:', peerId)
    })
    
    this.p2p.on('peer-disconnect', (peerId) => {
      // Remove participant
      this.session.removeParticipant(peerId)
      this._emit('participants-update', this.session.participants)
      this._broadcastState()
    })
    
    this.p2p.on('message', (msg) => {
      this._handlePresenterMessage(msg)
    })
  }

  _handlePresenterMessage(msg) {
    switch (msg.type) {
      case MESSAGE_TYPES.JOIN:
        this.session.addParticipant(msg.from, msg.name)
        this._emit('participants-update', this.session.participants)
        // Send current state to new participant
        this.p2p.sendTo(msg.from, {
          type: MESSAGE_TYPES.SYNC_STATE,
          session: this._getSessionState()
        })
        break
        
      case MESSAGE_TYPES.VOTE:
        this._handleVote(msg.from, msg.pollId, msg.data)
        break
        
      case MESSAGE_TYPES.SUBMIT:
        this._handleSubmit(msg.from, msg.pollId, msg.data)
        break
    }
  }

  _handleVote(userId, pollId, data) {
    const poll = this._pollInstances.get(pollId)
    if (!poll || !(poll instanceof MultipleChoicePoll)) return
    
    try {
      poll.vote(userId, data.selections)
      this._emit('poll-update', { pollId, poll })
      this._broadcastPollUpdate(pollId)
    } catch (err) {
      console.error('Vote error:', err)
    }
  }

  _handleSubmit(userId, pollId, data) {
    const poll = this._pollInstances.get(pollId)
    if (!poll) return
    
    try {
      if (poll instanceof WordCloudPoll) {
        poll.submit(userId, data.words)
      } else if (poll instanceof OpenEndedPoll) {
        poll.submit(userId, data.text)
      }
      this._emit('poll-update', { pollId, poll })
      this._broadcastPollUpdate(pollId)
    } catch (err) {
      console.error('Submit error:', err)
    }
  }

  _setupParticipantHandlers() {
    this.p2p.on('message', (msg) => {
      this._handleParticipantMessage(msg)
    })
    
    this.p2p.on('disconnected', () => {
      this.connectionStatus = 'disconnected'
      this._emit('status-change', this.connectionStatus)
    })
  }

  _handleParticipantMessage(msg) {
    switch (msg.type) {
      case MESSAGE_TYPES.SYNC_STATE:
        this._restoreSessionState(msg.session)
        this._emit('session-sync', this.session)
        break
        
      case MESSAGE_TYPES.POLL_UPDATE:
        this._updatePollFromData(msg.pollId, msg.pollData)
        this._emit('poll-update', { pollId: msg.pollId })
        break
        
      case MESSAGE_TYPES.PARTICIPANT_UPDATE:
        if (this.session) {
          this.session.participants = msg.participants
          this._emit('participants-update', msg.participants)
        }
        break
    }
  }

  /**
   * Create a new poll
   */
  createPoll(type, config) {
    const poll = createPoll(type, config)
    this._pollInstances.set(poll.id, poll)
    
    this.session.addPoll({
      id: poll.id,
      type,
      question: config.question,
      options: config.options
    })
    
    this._emit('polls-update', this.session.polls)
    return poll
  }

  /**
   * Activate a poll for voting
   */
  activatePoll(pollId) {
    this.session.setActivePoll(pollId)
    this._emit('active-poll-change', pollId)
    this._broadcastState()
  }

  /**
   * Submit a vote/response (for participants)
   */
  submitVote(pollId, selections) {
    this.p2p.sendToHost({
      type: MESSAGE_TYPES.VOTE,
      pollId,
      data: { selections }
    })
  }

  submitWords(pollId, words) {
    this.p2p.sendToHost({
      type: MESSAGE_TYPES.SUBMIT,
      pollId,
      data: { words }
    })
  }

  submitText(pollId, text) {
    this.p2p.sendToHost({
      type: MESSAGE_TYPES.SUBMIT,
      pollId,
      data: { text }
    })
  }

  /**
   * Get a poll instance
   */
  getPoll(pollId) {
    return this._pollInstances.get(pollId)
  }

  _getSessionState() {
    const polls = {}
    for (const [id, poll] of this._pollInstances) {
      polls[id] = poll.toJSON()
    }
    
    return {
      code: this.session.code,
      activePollId: this.session.activePollId,
      participants: this.session.participants,
      polls
    }
  }

  _restoreSessionState(state) {
    this.session = new Session()
    this.session.code = state.code
    this.session.activePollId = state.activePollId
    this.session.participants = state.participants || []
    
    // Restore poll instances
    for (const [id, pollData] of Object.entries(state.polls || {})) {
      let poll
      switch (pollData.type) {
        case 'multiple-choice':
          poll = MultipleChoicePoll.fromJSON(pollData)
          break
        case 'word-cloud':
          poll = WordCloudPoll.fromJSON(pollData)
          break
        case 'open-ended':
          poll = OpenEndedPoll.fromJSON(pollData)
          break
      }
      if (poll) {
        this._pollInstances.set(id, poll)
        this.session.polls.push({
          id: poll.id,
          type: pollData.type,
          question: poll.question
        })
      }
    }
  }

  _updatePollFromData(pollId, data) {
    // Update poll instance from broadcast data
    const existing = this._pollInstances.get(pollId)
    if (!existing) return
    
    // Merge the new data
    if (data.results) {
      // For display purposes, store latest results
      existing._latestResults = data.results
    }
  }

  _broadcastState() {
    this.p2p.broadcast({
      type: MESSAGE_TYPES.SYNC_STATE,
      session: this._getSessionState()
    })
  }

  _broadcastPollUpdate(pollId) {
    const poll = this._pollInstances.get(pollId)
    if (!poll) return
    
    this.p2p.broadcast({
      type: MESSAGE_TYPES.POLL_UPDATE,
      pollId,
      pollData: poll.toJSON()
    })
  }

  /**
   * Cleanup
   */
  destroy() {
    if (this.p2p) {
      this.p2p.destroy()
    }
    this.session = null
    this._pollInstances.clear()
  }
}
