/**
 * Poll Types for Mentimeter Pro
 */

/**
 * Factory function to create polls
 * @param {string} type - Poll type
 * @param {Object} config - Poll configuration
 * @returns {Poll} Poll instance
 */
export function createPoll(type, config) {
  switch (type) {
    case 'multiple-choice':
      return new MultipleChoicePoll(config)
    case 'word-cloud':
      return new WordCloudPoll(config)
    case 'open-ended':
      return new OpenEndedPoll(config)
    default:
      throw new Error(`Unknown poll type: ${type}`)
  }
}

/**
 * Base Poll class
 */
class BasePoll {
  constructor(config) {
    this.id = crypto.randomUUID()
    this.question = config.question
    this.createdAt = Date.now()
  }
}

/**
 * Multiple Choice Poll
 */
export class MultipleChoicePoll extends BasePoll {
  constructor(config) {
    super(config)
    this.type = 'multiple-choice'
    this.options = config.options || []
    this.allowMultiple = config.allowMultiple || false
    this.votes = new Map() // userId -> [selectedOptions]
  }

  /**
   * Record a vote
   * @param {string} userId - User ID
   * @param {string[]} selectedOptions - Selected option(s)
   */
  vote(userId, selectedOptions) {
    // Validate options
    for (const option of selectedOptions) {
      if (!this.options.includes(option)) {
        throw new Error(`Invalid option: ${option}`)
      }
    }
    
    // If not allowing multiple, only keep first
    const toStore = this.allowMultiple ? selectedOptions : [selectedOptions[0]]
    this.votes.set(userId, toStore)
  }

  /**
   * Get vote counts per option
   * @returns {Object} Option -> count mapping
   */
  getResults() {
    const results = {}
    for (const option of this.options) {
      results[option] = 0
    }
    
    for (const selections of this.votes.values()) {
      for (const selection of selections) {
        results[selection]++
      }
    }
    
    return results
  }

  /**
   * Get total number of votes
   * @returns {number} Total votes
   */
  getTotalVotes() {
    let total = 0
    for (const selections of this.votes.values()) {
      total += selections.length
    }
    return total
  }

  /**
   * Get percentages for each option
   * @returns {Object} Option -> percentage mapping
   */
  getPercentages() {
    const results = this.getResults()
    const total = this.getTotalVotes()
    const percentages = {}
    
    for (const option of this.options) {
      percentages[option] = total > 0 ? Math.round((results[option] / total) * 100) : 0
    }
    
    return percentages
  }

  /**
   * Reset all votes
   */
  reset() {
    this.votes.clear()
  }

  /**
   * Serialize to JSON
   * @returns {Object} JSON representation
   */
  toJSON() {
    return {
      id: this.id,
      type: this.type,
      question: this.question,
      options: this.options,
      allowMultiple: this.allowMultiple,
      votes: Object.fromEntries(this.votes),
      createdAt: this.createdAt
    }
  }

  /**
   * Restore from JSON
   * @param {Object} json - JSON representation
   * @returns {MultipleChoicePoll} Restored poll
   */
  static fromJSON(json) {
    const poll = new MultipleChoicePoll({
      question: json.question,
      options: json.options,
      allowMultiple: json.allowMultiple
    })
    poll.id = json.id
    poll.createdAt = json.createdAt
    poll.votes = new Map(Object.entries(json.votes || {}))
    return poll
  }
}

/**
 * Word Cloud Poll
 */
export class WordCloudPoll extends BasePoll {
  constructor(config) {
    super(config)
    this.type = 'word-cloud'
    this.maxWords = config.maxWords || 3
    this.submissions = new Map() // userId -> [words]
  }

  /**
   * Normalize a word (lowercase, strip special chars)
   * @param {string} word - Raw word
   * @returns {string} Normalized word
   */
  _normalizeWord(word) {
    return word.toLowerCase().trim().replace(/[^a-zäöüß0-9]/gi, '')
  }

  /**
   * Submit words from a user
   * @param {string} userId - User ID
   * @param {string[]} words - Array of words
   */
  submit(userId, words) {
    const normalized = words
      .map(w => this._normalizeWord(w))
      .filter(w => w.length > 0)
      .slice(0, this.maxWords)
    
    this.submissions.set(userId, normalized)
  }

  /**
   * Get all words with their counts
   * @returns {Array} Array of {text, count} objects
   */
  getWords() {
    const wordCounts = new Map()
    
    for (const words of this.submissions.values()) {
      for (const word of words) {
        wordCounts.set(word, (wordCounts.get(word) || 0) + 1)
      }
    }
    
    return Array.from(wordCounts.entries())
      .map(([text, count]) => ({ text, count }))
      .sort((a, b) => b.count - a.count)
  }

  /**
   * Get words with calculated font sizes for display
   * @param {number} minSize - Minimum font size
   * @param {number} maxSize - Maximum font size
   * @returns {Array} Array of {text, count, size} objects
   */
  getWordsWithSizes(minSize = 16, maxSize = 48) {
    const words = this.getWords()
    if (words.length === 0) return []
    
    const maxCount = Math.max(...words.map(w => w.count))
    const minCount = Math.min(...words.map(w => w.count))
    const range = maxCount - minCount || 1
    
    return words.map(w => ({
      ...w,
      size: minSize + ((w.count - minCount) / range) * (maxSize - minSize)
    }))
  }

  /**
   * Serialize to JSON
   */
  toJSON() {
    return {
      id: this.id,
      type: this.type,
      question: this.question,
      maxWords: this.maxWords,
      submissions: Object.fromEntries(this.submissions),
      createdAt: this.createdAt
    }
  }

  /**
   * Restore from JSON
   */
  static fromJSON(json) {
    const poll = new WordCloudPoll({
      question: json.question,
      maxWords: json.maxWords
    })
    poll.id = json.id
    poll.createdAt = json.createdAt
    poll.submissions = new Map(Object.entries(json.submissions || {}))
    return poll
  }
}

/**
 * Open Ended Poll
 */
export class OpenEndedPoll extends BasePoll {
  constructor(config) {
    super(config)
    this.type = 'open-ended'
    this.maxLength = config.maxLength || 500
    this.responses = []
  }

  /**
   * Submit a text response
   * @param {string} userId - User ID
   * @param {string} text - Response text
   */
  submit(userId, text) {
    const trimmed = text.trim()
    
    if (!trimmed) {
      throw new Error('Response cannot be empty')
    }
    
    const truncated = trimmed.slice(0, this.maxLength)
    
    this.responses.push({
      id: crypto.randomUUID(),
      userId,
      text: truncated,
      timestamp: Date.now()
    })
  }

  /**
   * Get all responses
   * @returns {Array} Array of response objects
   */
  getResponses() {
    return [...this.responses].sort((a, b) => a.timestamp - b.timestamp)
  }

  /**
   * Get response count
   * @returns {number} Number of responses
   */
  getResponseCount() {
    return this.responses.length
  }

  /**
   * Serialize to JSON
   */
  toJSON() {
    return {
      id: this.id,
      type: this.type,
      question: this.question,
      maxLength: this.maxLength,
      responses: this.responses,
      createdAt: this.createdAt
    }
  }

  /**
   * Restore from JSON
   */
  static fromJSON(json) {
    const poll = new OpenEndedPoll({
      question: json.question,
      maxLength: json.maxLength
    })
    poll.id = json.id
    poll.createdAt = json.createdAt
    poll.responses = json.responses || []
    return poll
  }
}
